import { createAsyncThunk } from "@reduxjs/toolkit";
import proxiedFetch from "./proxiedFetch";
// import { useDispatch } from "react-redux";
// import fileBrowserActions from '../store/store';

const fetchFiles = createAsyncThunk("fetchFiles", async ({channelId, lastMessageId, callback}, ) => {

  // console.log("Fetch files dispatch")
  // console.log("Arguments: ", {channelId, lastMessageId, callback})

  var requestOptions = {
    channelId,
    lastMessageId
  };

  const data = await proxiedFetch("/files", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(requestOptions),
  })
    .then((res) => res.json())
    .then((data) => data);

    callback();

    // console.log("Return values: ", {...data, firstRequest: !lastMessageId})

  return {...data, firstRequest: !lastMessageId};
});

export default fetchFiles;
